import React from 'react';
import TaskItem from './TaskItem';
import { connect } from 'react-redux';

class TaskList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            filterName: '',
            filterStatus: -1
            // all: -1  ||  active: 1  ||  inactive: 0
        };
    }

    onChangeFilterValue = (event) => {
        const name = event.target.name;
        var value = event.target.value;

        if (name === 'filterStatus') {
            value = Number(value)
        } else {
            value = value.toLowerCase();
        }

        this.setState({
            [name]: value
        });
    }

    render() {
        const { tasks, keyword } = this.props;

        const { filterName, filterStatus } = this.state;
        let filteredTasks = Object.assign([], tasks);

        /* START FILTER TASKS */
        // filter by name base on name filter input
        if (filterName) {
            filteredTasks = filteredTasks.filter((task) => {
                return task.name.toLowerCase().indexOf(filterName) !== -1;
            });
        }

        // filter by status
        filteredTasks = filteredTasks.filter((task) => {
            return filterStatus === -1
                ? true
                : task.status === (filterStatus === 1)
        });

        // filter by name base on search keyword
        const elementTasks = filteredTasks.map((task, index) => {
            if ((keyword && task.name.toLowerCase().indexOf(keyword) !== -1) || !keyword) {
                return <TaskItem
                    key={index}
                    index={index}
                    task={task} />
            }
            return null
        });
        /* END FILTER TASKS */

        return (
            <div className="panel panel-warning">
                <table border="1" className="table table-bordered table-hover">
                    <thead>
                        <tr>
                            <th className="text-center">ID</th>
                            <th className="text-center">Name</th>
                            <th className="text-center">Status</th>
                            <th className="text-center">Action</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td></td>
                            <td>
                                <input
                                    className="form-control"
                                    type="text"
                                    name="filterName"
                                    value={filterName}
                                    onChange={this.onChangeFilterValue} />
                            </td>
                            <td>
                                <select
                                    className="form-control"
                                    name="filterStatus"
                                    value={filterStatus}
                                    onChange={this.onChangeFilterValue}>
                                    <option value={-1}>All</option>
                                    <option value={0}>Inactive</option>
                                    <option value={1}>Active</option>
                                </select>
                            </td>
                            <td></td>
                        </tr>

                        {elementTasks}
                    </tbody>
                </table>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        tasks: state.tasks,
        keyword: state.keyword
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(TaskList)